<template>
<b-container fluid>
   <b-row class="my-1" style="width: 600px;margin: auto;">
    <b-col sm="4">
      <label for="input-live">StartSalary:</label>
    </b-col>
     <b-col sm="8">
    <b-form-input
      id="input-live"
      v-model="salary"
      :state="salaryState"
      aria-describedby="input-live-help input-live-salary"
      placeholder="Input your salary"
      trim
    ></b-form-input>

    <!-- This will only be shown if the preceding input has an invalid state -->
    <b-form-invalid-feedback id="input-live-salary">
      Do not Input Salary Less Than 1
    </b-form-invalid-feedback>
    </b-col></b-row>
    <b-row class="my-1" style="width: 600px;margin: auto;">
 <b-col sm="4">
      <label for="input-live">Increment:(%)</label>
    </b-col>
     <b-col sm="8">
    <b-form-input
      id="input-live"
      v-model="inquota"
      :state="inquotaState"
      placeholder="Input your Increase Quota:"
      aria-describedby="input-live-help input-live-inquota"
      trim
    ></b-form-input>

    <!-- This will only be shown if the preceding input has an invalid state -->
    <b-form-invalid-feedback id="input-live-inquota">
      Do not Input Negative Number
    </b-form-invalid-feedback></b-col></b-row>

<b-row class="my-1" style="width: 600px;margin: auto;">
    <b-col sm="4">
      <label for="input-live">Deductions:(%)</label>
    </b-col>
     <b-col sm="8">
    <b-form-input
      id="input-live"
      v-model="dequota"
      :state="dequotaState"
      aria-describedby="input-live-help input-live-dequota"
      placeholder="Input your Decrease Quota:"
      trim
    ></b-form-input>

    <!-- This will only be shown if the preceding input has an invalid state -->
    <b-form-invalid-feedback id="input-live-dequota">
      Do not Input Negative Number
    </b-form-invalid-feedback>
     </b-col></b-row>
     <b-row class="my-1" style="width: 600px;margin: auto;">
     <b-col sm="4">
      <label for="input-live">Frequently:</label>
    </b-col>
     <b-col sm="8">
        <b-form-select v-model="count" :options="options"></b-form-select>
     </b-col></b-row>
     <b-row class="my-1" style="width: 600px;margin: auto;">
     <b-col sm="8"></b-col>
     <b-col sm="4" style="text-align: end;">
        <b-button variant="success" @click="querysalary()">Button</b-button></b-col></b-row>
    <!-- This is a form text block (formerly known as help block) -->
    <!-- <b-form-text id="input-live-help">Your full name.</b-form-text> -->
    <b-row class="my-1">
     <b-col sm="4">
      <Inreport :inreport="inreport"/>
     </b-col>
     <b-col sm="4">
      <Dereport :dereport="dereport"/>
     </b-col>
     <b-col sm="4">
      <Predictreport :predict="predict"/>
     </b-col>
    </b-row>
  </b-container>

</template>

<script>
import axios from 'axios'
import Inreport from './Inreport'
import Dereport from './Dereport'
import Predictreport from './Predictreport'
export default {
  name: 'Salary',
  components: {
    Inreport,
    Dereport,
    Predictreport
  },
  computed: {
      salaryState() {
        return this.salary > 1 ? true : false
      },
      inquotaState() {
        return this.inquota > 0 ? true : false
      },
      dequotaState() {
        return this.dequota > 0 ? true : false
      }

    },
    data() {
      return {
        salary: '',
        inquota: '',
        dequota: '',
        inreport: [],
        dereport: [],
        predict: [],
        count: null,
        options: [
          { value: null, text: 'Please select an option' },
          { value: '3', text: 'Quarterly' },
          { value: '6', text: 'Half-Yearly' },
          { value: '12', text: 'Annually' }
        ]
      }
    },
    methods: {
       querysalary() {
        axios.post('/increase', {"salary": this.salary, "inquota": this.inquota, "dequota": this.dequota, "count": this.count})
        .then(res => {
          this.inreport = res.data.data
        })
        axios.post('/decrease', {"salary": this.salary, "inquota": this.inquota, "dequota": this.dequota, "count": this.count})
        .then(res => {
          this.dereport = res.data.data
        })
        axios.post('/predict', {"salary": this.salary, "inquota": this.inquota, "dequota": this.dequota, "count": this.count})
        .then(res => {
          this.predict = res.data.data
        })
      }
    }
}
</script>

